import viewDatas from './view.config'
import * as postNames from '../../service/userManger'
import {message} from 'antd'
export default {
    namespace:'addUser',
    state:{
        viewDatas
    },
    reducers:{
        changeTitleIndex(state,{index,titleIndex}){
            state.viewDatas[index].titleIndex = titleIndex;
            return JSON.parse(JSON.stringify(state));
        },
        changeWritedVal(state,{index,key,value,resource}){
            if(key === 'view_id'){
                let view_authority_text = resource.find(item=>item.view_id === value).view_authority_text;
                state.viewDatas[index].writedData['view_authority_text'] = view_authority_text;
            }
            state.viewDatas[index].writedData[key] = value;
            return JSON.parse(JSON.stringify(state));
        },
        changeStatus(state,{index,value}){
            state.viewDatas[index].status = value;
            return JSON.parse(JSON.stringify(state));
        },
        resetWritedVal(state,{index}){
            state.viewDatas[index].writedData = {};
            return JSON.parse(JSON.stringify(state));
        }
    },
    effects:{
        *postData(payload,{call,put,select}){
            let {identity} = yield select(store=>store.showUser);
            let {
                postName,
                index,
                writedData,
                resourceName,
                resource,
                titleIndex,
                idKey
            } = payload;
            yield put({type:'changeStatus',index,value:3});
            let result = yield call(postNames[postName],writedData);
            if(result.code === 1){
                yield put({type:'changeStatus',index,value:1});
                if(titleIndex === 0){
                    if(resourceName === 'user' && writedData.identity_id){
                        let {identity_text} = identity.find(item=>item.identity_id === writedData.identity_id);
                        resource.push({...writedData,[idKey]:result[idKey],identity_text});
                    }else{
                        resource.push({...writedData,[idKey]:result[idKey]});
                    }
                }else if(titleIndex === 1){
                    let index = resource.findIndex(item=>item[idKey] === writedData[idKey]);
                    if(resourceName === 'user' && writedData.identity_id){
                        let {identity_text} = identity.find(item=>item.identity_id === writedData.identity_id);
                        resource[index] = {...resource[index],...writedData,identity_text};
                    }else{
                        resource[index] = {...resource[index],...writedData};
                    }
                }
                yield put({type:'showUser/changeState',[resourceName]:resource})
                message.success(result.msg,1);
            }else{
                yield put({type:'changeStatus',index,value:2});
                message.error(result.msg,1);
            }

        }
    }
}